header {
    background: cadetblue;
    color: white;
    padding: 1.5rem 0;
    text-align: center;
}

main {
    padding: 1rem 0;
}

footer {
    border-top: 1px solid #eee;
    padding-top: 1rem;
    text-align: center;
    font-size: .8rem;
}

.container {
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.wrapper {
    max-width: 60%;
    margin: 0 auto;
}

label {
    display: block;
    font-size: 0.75rem;
}

input[type="text"] {
    width: 200px;
    height: 40px;
    outline: none;
}

.btns {
    text-align: center;
    margin-top: 1rem;
}

button {
    border: 0;
    width: 80px;
    margin: 1rem .5rem;
}

button:hover {
  background: blue;
  color: white;;
}

.inverse {
    background: transparent;
    border: 2px solid blue;
    color: black;
}

.reset {
    background: red;
    border: 1px solid transparent;
    color: white;
}